import * as React from 'react';
import { match } from 'react-router';
import { observer } from 'mobx-react';
import { mediaItemStore } from './mediaItem.store';
import { reaction } from 'mobx';

interface Props {
  match?: match<{ id: string }>;
}
@observer
export class MediaItems extends React.Component<Props> {
  componentDidMount() {
    const id = this.props.match!.params.id;
    mediaItemStore.getData(id);
    reaction(() => mediaItemStore.comments, (comments) => console.log(comments));

  }

  render() {
    // const test = mediaItemStore.comments.map((comment, index) => {
    //   return <div key={index}>{comment}</div>;
    // });
    let MediaItemComponent = <div/>;
    if (mediaItemStore.mediaItem !== undefined) {
      MediaItemComponent = (
        <div className="media-item-component">
          <div className="media-item-component__media-img">
            <img src={mediaItemStore.mediaItem.images.standard_resolution.url}/>
          </div>
          <div className="media-item-component__user-profile">
            <div className="user-profile__profile-picture">
              <img src={mediaItemStore.mediaItem.user.profile_picture} alt=""/>
            </div>
            <div className="user-profile__username">
              <div>{mediaItemStore.mediaItem.user.username}</div>
            </div>
          </div>
          <div className="media-item-component__media-details">
            <div className="media-details__like-count">
              <div>{mediaItemStore.mediaItem.likes.count}</div>
            </div>
            <div className="media-details__create_time">
              <div>{mediaItemStore.mediaItem.created_time}</div>
            </div>
          </div>
          {/*{test}*/}

        </div>
      );
    }
    return (
      <div>

        {MediaItemComponent}
      </div>
    );
  }
}


